.promo {
  position: relative;
  padding-top: 110px;
  padding-bottom: 74px;
  background-color: rgba(16, 15, 15, 0.66);
  color: $default-color;
  overflow: hidden;

  @media (max-width: $table) {
    padding-top: 55px;
    padding-bottom: 40px;
  }


  &::before {
    content: "";
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(16, 15, 15, 0.66);
  }

  &__slider {
    z-index: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    & .swiper-container {
      width: 100%;
      height: 100%;
    }

    & .swiper-slide {
      background-size: cover;
      background-position: center;
    }
  }

  &__wrapper {
    position: relative;
    z-index: 1;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__info {
    width: 50%;
    color: #ffffff;

    @media (max-width: $table) {
      width: 100%;
      padding-bottom: 20px;
      text-align: center;
    }

    & h1 {
      margin: 0;
      padding-bottom: 37px;
      font-family: $mainFontMedium;
      font-style: normal;
      font-weight: bold;
      font-size: 52px;
      line-height: 62px;
      /* or 119% */

      text-transform: uppercase;

      @media (max-width: $mobile) {
        font-size: 42px;
        line-height: 42px;
      }
    }
  }

  &__dop-text {
    padding-bottom: 32px;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 18px;

    letter-spacing: 0.08em;
    text-transform: uppercase;
  }

  &__desc-text {
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 24px;
  }

  &__form {
    width: 50%;
    max-width: 400px;
    padding: 36px 32px;
    background: rgba(51, 51, 51, 0.6);
    border-radius: 6px;

    @media (max-width: $table) {
      width: 100%;
      margin: 0 auto;
    }

    & h2 {
      margin: 0;
      padding-bottom: 46px;
      font-style: normal;
      font-weight: normal;
      font-size: 26px;
      line-height: 26px;
    }
  }
}
