.feedback {
  background-color: #9da1a6;
  background-image: url(../img/feedback/feedback-pic.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-bottom: 52px;
  padding-top: 78px;
  color: #ffffff;

  @media (max-width: $table) {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  & h2 {
    margin: 0;
    padding-bottom: 12px;
    font-style: normal;
    font-weight: bold;
    font-size: 32px;
    line-height: 46px;

    text-transform: uppercase;
  }

  & p {
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 34px;

    color: #bdbdbd;
  }

  &__form.form {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 46px;
    max-width: 980px;

    & label {
      width: 47%;

      @media (max-width: $table) {
        width: 100%;
      }
    }

    & button[type="submit"] {
      width: 100%;
    }

    & a {
      margin-bottom: 34px;
      font-weight: bold;
      font-size: 12px;
      line-height: 16px;

      letter-spacing: 0.02em;
      text-decoration-line: underline;

      color: #ffffff;
    }
  }

  &__form-bottom {
    display: flex;
    flex-direction: column;
    width: 47%;

    @media (max-width: $table) {
      width: 100%;
    }
  }
}
