.section {
  padding-top: 105px;
  padding-bottom: 0;

  @media (max-width: $table) {
    padding-top: 55px;
  }

  & h2 {
    margin: 0;
    text-align: center;
    font-style: normal;
    font-weight: bold;
    font-size: 32px;
    line-height: 46px;

    text-transform: uppercase;

    background: -webkit-linear-gradient(
      91.17deg,
      #eb5d56 0.01%,
      #390a40 100.01%
    );
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}
