.page-footer {
  padding-top: 50px;
  padding-bottom: 20px;

  transition: 1s;

  @media (max-width: $table) {
    padding-top: 20px;
    padding-bottom: 20px;
  }

  &__wrapper {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    width: 100%;

    @media (max-width: $table) {
      align-items: flex-start;
    }

    @media (max-width: 575px) {
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
  }

  &__right {
    display: flex;
    flex-direction: column;

    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 26px;

    @media (max-width: $table) {
      order: 2;
      padding-bottom: 15px;
    }
    
  }

  &__logo {
    @media (max-width: $table) {
      order: 1;
      padding-bottom: 15px;
    }
  }

  &__left {
    display: flex;
    flex-direction: column;

    font-style: normal;
    font-weight: 300;
    font-size: 12px;
    line-height: 14px;

    @media (max-width: $table) {
      order: 3;
      text-align: center;
    }
  }

  &__copy {
    padding-bottom: 20px;
    color: #828282;
  }

  &__conf {
    padding-bottom: 20px;
    color: #828282;
    transition: $trasition;

    &:focus,
    &:hover {
      opacity: 0.5;
    }
  }

  &__mail {
    color: #4f4f4f;
    transition: $trasition;
    text-decoration: none;

    &:focus,
    &:hover {
      opacity: 0.5;
      text-decoration: none;
    }
  }

  &__phone {
    padding-bottom: 16px;
    color: #4f4f4f;
    transition: $trasition;
    text-decoration: none;

    &:focus,
    &:hover {
      opacity: 0.5;
      text-decoration: none;
    }
  }
}
