.categorys {
  &__items {
    padding-top: 0;
    max-width: 1265px;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  &__item {
    flex-basis: 355px;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

    color: #4f4f4f;

    text-decoration: none;

    @media (max-width: $table) {
      flex-basis: 200px;
    }

    & img {
      object-fit: contain;
    }
  }

  &__item-pic {
    padding-bottom: 30px;
    height: 310px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    @media (max-width: $table) {
      height: 200px;
    }
  }

  &__item-title {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    margin: 0;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 34px;

    & svg {
      width: 200px;
      padding-bottom: 12px;
    }
  }
}
