.accordion {
  padding-top: 34px;
  &__item {
    position: relative;

    border-bottom: 1px solid #f0f0f0;

    &--active {
      & .accordion__item-text {
        display: block;
        animation-name: fadeInDown;
      }

      & .accordion__item-icon {
        transform: rotate(0deg);
        transition: $trasition;
      }
    }

    &--big {
      & .accordion__item-header {
        padding-top: 30px;
        padding-bottom: 20px;

        & h3 {
          padding-top: 10px;
        }
      }

      & .accordion__item-icon {
        padding-top: 0;
      }

      & .accordion__item-number {
        padding-top: 0;
      }
    }
  }

  &__item-header {
    display: flex;
    align-items: center;
    padding-top: 25px;
    padding-bottom: 25px;
    // padding-right: 36px;

    // @media (max-width: 959px) {
    //   padding-right: 0;
    // }

    // @media (max-width: $mobile) {
    //   padding-top: 31px;
    //   padding-bottom: 15px;
    // }

    &:hover {
      cursor: pointer;
    }

    & h3 {
      margin: 0;
      padding-left: 10px;
      width: 90%;
      font-style: normal;
      font-weight: normal;
      font-size: 24px;
      line-height: 34px;
      color: $primary-color;

      @media (max-width: $mobile) {
        width: 83%;
        font-size: 16px;
        line-height: 160%;
      }
    }
  }

  &__item-icon {
    padding-top: 10px;
    width: 20px;
    height: 20px;
    fill: transparent;
    margin-left: auto;
    transform: rotate(180deg);
    transition: $trasition;

    @media (max-width: $mobile) {
      padding-top: 0;
    }
  }

  &__item-text {
    display: none;
    animation-name: fadeInUp;
    // margin-bottom: 26px;
    // padding-right: 35px;
    padding: 18px 20px;

    font-weight: 300;
    font-size: 16px;
    line-height: 24px;

    background: rgba(196, 196, 196, 0.06);
    color: $primary-color;

    @media (max-width: $mobile) {
      margin-bottom: 6px;
    }
  }
}
