.about {
  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-left: 0;

    @media (max-width: $table) {
      padding-left: 15px;
    }
  }

  &__pic {
    width: 55%;

    @media (max-width: $table) {
      width: 100%;
    }
  }

  &__info {
    width: 45%;
    padding-left: 50px;
    box-sizing: border-box;
    text-align: left;

    @media (max-width: $table) {
      width: 100%;
      padding-left: 0;
    }

    & h2 {
      text-align: left;

      @media (max-width: $table) {
        text-align: center;
      }
    }
  }

  &__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 70px;

    @media (max-width: $table) {
      padding-top: 20px;
    }
  }

  &__item {
    width: 50%;

    @media (max-width: $table) {
      text-align: center;
      width: 100%;
    }
  }
}
