.form {
  display: flex;
  flex-wrap: wrap;
  background-color: transparent;
  color: $default-color;
  box-sizing: border-box;

  transition: 1s;

  @media (max-width: 753px) {
    flex-direction: column;
    align-items: flex-end;
  }

  @media (max-width: 575px) {
    align-items: center;
    width: 100%;
  }

  & label {
    width: 100%;
    padding-bottom: 20px;
    display: flex;
    flex-direction: column;

    & span {
      font-family: $mainFontMedium;
      display: block;
      padding-bottom: 12px;
      font-style: normal;
      font-weight: bold;
      font-size: 12px;
      line-height: 16px;

      letter-spacing: 0.02em;
    }
  }

  & input {
    padding: 14px 12px;
    width: 100%;
    height: 100%;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 16px;
    /* identical to box height, or 100% */

    background-color: $default-color;
    border: none;
    border-radius: 4px;
    box-sizing: border-box;
    color: rgba(0, 0, 0, 0.5);

    outline: none;

    @media (max-width: $mobile) {
      padding: 16px 15px;
    }

    &::placeholder {
      color: rgba(0, 0, 0, 0.5);
      opacity: 1;
    }

    &:focus {
      opacity: 1;
    }
  }

  & input:valid {
    border-color: green;
  }

  & input:invalid {
    border-color: red;
  }

  & input:required:valid {
    border-color: green;
  }

  & button[type="submit"] {
    padding: 12px;
    margin-bottom: 30px;
    width: 100%;
    color: inherit;
    font-family: $mainFontMedium;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 160%;
    color: $default-color;
    background: linear-gradient(91.17deg, #eb5d56 0.01%, #390a40 100.01%);
    border-radius: 4px;
    border: none;
    box-sizing: border-box;
    cursor: pointer;

    outline: none;
    transition: 0.5s;

    text-transform: uppercase;

    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));

    transition: $trasition;

    &:focus,
    &:hover {
      transition: $trasition;
      background: linear-gradient(91.17deg, #812c49 0.01%, #390a40 100.01%);
    }
  }

  &__text {
    margin: 0;
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 18px;
    letter-spacing: 0.5px;
  }
}
