.nav {
  &__list {
    display: flex;
    flex-wrap: wrap;

    padding: 0;
    margin: 0;
    list-style: none;

    @media (max-width: $table) {
      flex-direction: column;
      width: 100%;
    }
  }

  &__item {
    position: relative;

    &:not(:last-child) {
      margin-right: 27px;
    }

    @media (min-width: $table) {
      &:hover {
        & .nav__sub-list {
          display: block;
        }

        & .nav__link {
          color: #9c69e2;
        }

        & .nav__link svg {
          fill: #9c69e2;
          transform: translateY(0%) rotate(180deg);
          transition: transform 0.5s;
        }
      }
    }

    &--active {
      & .nav__sub-list {
        display: flex;
        flex-direction: column;
        height: auto;

        animation-duration: 0.5s;
        transition: height 1s;
      }

      & .nav__link {
        color: #9c69e2;
      }

      & .nav__link svg {
        fill: #9c69e2;
        transform: translateY(-50%) rotate(180deg);
        transition: transform 0.5s;

        @media (max-width: $table) {
          transform: translateY(-50%) rotate(0deg);
          transition: transform 0.5s;
        }
      }
    }

    @media (max-width: $table) {
      border-top: 1px solid #d6b9ff;
      margin-right: 0;

      &:not(:last-child) {
        margin-right: 0;
      }

      &:last-child {
        border-bottom: 1px solid #d6b9ff;
      }
    }
  }

  &__link {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    padding: 10px 8px 18px;
    font-family: $mainFont;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 21px;

    color: #4f4f4f;

    text-decoration: none;
    box-sizing: border-box;
    transition: 0.5s;

    &:focus,
    &:hover {
      color: #3E0D41;

    }

    @media (max-width: $table) {
      display: block;
      padding: 0;
      padding-left: 32px;
      font-size: 27px;
      line-height: 277%;
    }

    @media (max-width: $mobile) {
      padding-left: 16px;
      font-size: 20px;
    }
  }
}
