.questions {
  position: relative;
  padding-top: 64px;
  padding-bottom: 100px;
  overflow: hidden;

  @media (max-width: $mobile) {
    padding-top: 0;
    padding-bottom: 0;
  }

  &__pic {
    width: 45%;
    height: auto;
    align-self: flex-start;
    box-sizing: border-box;

    @media (max-width: 1640px) {
      position: absolute;
      top: 130px;
      right: 0;
      display: flex;
      justify-content: flex-end;
    }

    @media (max-width: $table) {
      position: relative;
      top: 10%;
      width: 40%;
    }

    @media (max-width: 959px) {
      display: none;
      width: 100%;
    }

    &--mask {
      position: relative;

      @media (max-width: 1640px) {
        position: absolute;
      }

      @media (max-width: $table) {
        position: relative;
      }

      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        max-height: 983px;
        background-size: contain;
        background-repeat: no-repeat;
        background-image: url("../img/questions/mask-bd.png");

        overflow: hidden;
      }
    }

    & img {
      display: block;
      width: 99%;
      height: auto;
      margin: 3px;
      object-fit: contain;
    }
  }

  &__accordion {
    width: 52%;

    @media (max-width: 959px) {
      width: 100%;
      padding: 0 10%;
    }

    @media (max-width: $mobile) {
      padding: 0;
    }
  }

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    box-sizing: border-box;

    & h2 {
      text-align: left;
      padding-bottom: 34px;
      margin: 0;
      width: 100%;

      @media (max-width: $table) {
        text-align: center;
      }
    }
  }
}
